<template>
	<view>
		<view class="top">
			<image src="../../static/image/close.png" mode="widthFix" class="close-img"></image>
			<text @click="goRegister">注册</text>
		</view>
		<image src="../../static/image/logo.png" mode="widthFix" class="logo-img"></image>
		<view class="title">登录</view>
		<text class="subtitle">您好，欢迎来到天天聊天室！</text>
		<u-form :model="loginForm" ref="loginFormRef">
			<u-form-item prop="email"><u-input v-model="loginForm.email" placeholder="请输入邮箱" /></u-form-item>
			<u-form-item prop="password">
				<u-input v-model="loginForm.password" placeholder="请输入密码" type="password" :clearable="false" />
			</u-form-item>
		</u-form>
		<button type="default" @click="login" class="login-button">登录</button>
	</view>
</template>

<script>
export default {
	data() {
		return {
			loginForm: {
				email: '123456',
				password: 'zd44dw4ed4we4'
			},
			rules: {
				email: [
					{
						required: true,
						message: '请输入邮箱',
						// 可以单个或者同时写两个触发验证方式
						trigger: 'blur'
					}
				]
			}
		};
	},
	methods: {
		login() {
			uni.switchTab({
				url: '../index/index'
			});
		},
		goRegister() {
			uni.navigateTo({
				url: '../register/register'
			});
		}
	},
	// 必须要在onReady生命周期，因为onLoad生命周期组件可能尚未创建完毕
	onReady() {
		this.$refs.loginFormRef.setRules(this.rules);
	}
};
</script>

<style lang="scss" scoped>
.top {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 50rpx;
	.close-img {
		width: 34rpx;
	}
}
.logo-img {
	display: block;
	width: 170rpx;
	margin: 50rpx auto;
}
.title {
	font-size: 22px;
	font-weight: bold;
	margin-left: 50rpx;
}
.subtitle {
	font-size: 18px;
	color: #888;
	display: inline-block;
	margin: 30rpx 0;
	margin-left: 50rpx;
}
/deep/ .u-form-item {
	margin: 0 50rpx;
	height: 120rpx;
}
/deep/ .u-form-item__message {
	padding-left: 0 !important;
}
/deep/ .u-input {
	border-bottom: 1px solid #ddd;
}
.login-button {
	width: 500rpx;
	height: 90rpx;
	line-height: 90rpx;
	border-radius: 50px;
	margin: 100rpx auto;
	background-color: rgb(133, 203, 248);
	color: #fff;
	font-weight: bold;
	&::after {
		border: none;
	}
}
</style>
